export default {
  components: {
    BlogPosts: () => import('@/components/blog/posts/BlogPosts')
  },
  watch: {
    $route() {
      this.$store.dispatch('post/fetchPosts', {
        axios: this.$axios,
        query:
          '&category=' +
          this.$route.params.slug +
          '&status=published&limit=' +
          this.$store.state.settings.settings.blog.reading.postLimit
      })
    }
  },
  async created() {
    this.$store.state.post.page = 1
    await this.$store.dispatch('post/fetchPosts', {
      axios: this.$axios,
      query:
        '&category=' +
        this.$route.params.slug +
        '&status=published&limit=' +
        this.$store.state.settings.settings.blog.reading.postLimit
    })
  }
}
